.contact-form-container {
    width: 48%;
    align-self: center;
    margin: 30px 0;
}

.contact-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.input-box {
    background-color: #fff;
    width: 100%;
    border: 1px solid #e8e8e8;
    box-sizing: border-box;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.25);
    border-radius: 39px;
    padding: 19px 35px;
    margin: 10px 0;
    font-family: Poppins;
    font-size: 16px;
    line-height: 16px;
}

.body-input {
    height: 200px;
    padding: 25px 35px;
    font-size: 16px;
}

.contact-btn {
    margin: 10px 0;
    border: none;
    color: #fff;
    font-weight: bold;
    font-family: Poppins;
    font-style: normal;
    font-size: 16px;
    line-height: 16px;
    width: 100%;
    text-align: center;
    padding: 17px 35px;
    background: linear-gradient(39.97deg, #03d6f3 0.79%, #458ff6 79.29%);
    border-radius: 35px;
}

.social-icons-container{
    display: flex;
    justify-content: center;
    align-self: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;

}

.social-icon{
    filter:drop-shadow(0px 4px 4px rgba(0,0,0,0.25));
    margin: 2rem 15px;
}

.social-icon >img{
    height: 2.4rem;
    width: 2.4rem;
}

.social-icon:hover,
.conatct-btn:hover{
    cursor: pointer;
    transition: all .3s;
    filter:drop-shadow(0px 8px 8px rgba(0,0,0,0.5));
}

.vector-image{
    width: 70%;
    position: absolute;
    bottom: 0;
    left:0;
    z-index: -10;
}

.contact-image{
    display: block;
    max-width: 100%;
    max-height: 100%;
}

@media only screen and (max-width:640px){
    .social-icon{
        margin: 1.4rem 21px;
    }
    .contact-form-container {
        width: 80%;
    }
}