body{
    min-height: 100vh;
    margin: 0;
    width: 100%;
    box-sizing: border-box;
    overflow-x: hidden;
    font-family: Poppins, sans-serif;
}

input:focus,
textarea:focus,
select:focus{
    outline: none;
}

.section-container{
    box-sizing: border-box;
    min-height: 100vh;
    position: relative;
    padding: 45px 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-content: center;
}

/* For small screens */
@media only screen and (max-width:640px){
    .section-container{
        padding: 60px 0;
    }
}